import React from 'react';
import './Menu.css';
import { Link } from "react-router-dom";

class Menu extends React.Component {
    state = {
        open: false,
        menuItems: [
            {
                link: 'home',
                name: 'Home'
            },
            {
                link: 'map',
                name: 'Anfahrt'
            },
            {
                link: 'times',
                name: 'Öffnungszeiten'
            },
            {
                link: 'service',
                name: 'Leistungen'
            },
            {
                link: 'team',
                name: 'Team'
            },
            {
                link: 'history',
                name: 'Geschichte'
            }
        ]
    }
    render() {
        const { open, menuItems } = this.state
        return (
            <div className={`Menu ${open ? "open" : ""}`}>
                <div className="Menu-large Left">
                    {
                        menuItems && menuItems.filter( (item, ix) => ix < 3 ).map( (item, ix) => {
                            return (
                                <div className="Menu-item" key={`menuItemLeft${ix}`}>
                                    <Link className="Menu-item-link" to={{pathname: '/', hash: `#${item.link}`}}>{item.name}</Link>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="Menu-logo" alt="logo">

                </div>
                <div className="Menu-large right">
                    {
                        menuItems && menuItems.filter( (item, ix) => ix > 2 ).map( (item, ix) => {
                            return (
                                <div className="Menu-item" key={`menuItemRight${ix}`}>
                                    <Link className="Menu-item-link" to={`/#${item.link}`}>{item.name}</Link>
                                </div>
                            )
                        })
                    }
                </div>
                <div className="Menu-burger-wrap">
                    <button className="Menu-burger" onClick={() => { this._toggleMenu() }}>≡</button>
                </div>
                <div className={`Menu-mobile ${open ? "open" : ""}`}>
                    <ul className="Menu-mobile-list">
                        <div id="topOfNav"></div>
                        {
                            menuItems && menuItems.map((item, ix) => {
                                return (
                                    <li className="Menu-mobile-item" key={`menuItem${ix}`}>
                                        <Link className="Menu-mobile-item-link" to={`/#${item.link}`}>{item.name}</Link>
                                    </li>
                                )
                            })
                        }
                        <div id="bottomOfNav"></div>
                    </ul>
                </div>
            </div>
        );
    }
    _toggleMenu() {
        this.setState(({ open }) => ({
            open: !open
        }), () => {
            if (this.state.open) {
                document.getElementById("topOfMenu").scrollIntoView({behavior: "smooth"})
            }
        })
    }
}

export default Menu;
